// src/components/Profile.tsx

import { useGetAccountQuery } from "@/redux/api/profile";

const Profile = () => {
  const { data: user, isLoading, isError } = useGetAccountQuery();

  if (isLoading) return <div>Loading...</div>;
  if (isError || !user) return <div>Error loading the profile</div>;

  return (
    <div>
      <h1>Profile</h1>
      <p>User: {user.user}</p>
      <p>About: {user.about || "No details provided"}</p>
    </div>
  );
};

export default Profile;
